<template>
    <div class="az-footer">
        <div class="az-socail">
            <a href="mailto:albertaz1992@gmail.com">
                <az-icon name="envelope-o" scale="1" class="az-footer-social-icon"></az-icon>
            </a>
            <a href="https://www.twitter.com/Az1992Albert">
                <az-icon name="twitter" scale="1" class="az-footer-social-icon"></az-icon>
            </a>
            <a href="https://www.instagram.com/albertaz1992">
                <az-icon name="instagram" scale="1" class="az-footer-social-icon"></az-icon>
            </a>
            <a href="https://github.com/AlbertAZ1992">
                <az-icon name="github" scale="1" class="az-footer-social-icon"></az-icon>
            </a>

        </div>
        <div class="az-copy-right">
            <p>All work © Albert Zhang 2018</p>
        </div>

    </div>
</template>
<script>
import { LINKS } from '@/common/config';
import Icon from 'vue-awesome/components/Icon';

export default {
    name: 'az-footer',
    props: ['showHeaderClose'],
    components: {
        'az-icon': Icon,
    },
    data() {
        return {
            links: LINKS,
            isDark: false,
        };
    },
    mounted() {
        window.onscroll = () => {
            this.isDark = document.documentElement.scrollTop > window.innerHeight;
        };
    },
    methods: {
        showMenu() {
            this.$emit('showMenu');
        },
    },
};
</script>
<style lang="less">
@import '../common/css/main.less';
.az-footer {
    .az-socail {
        width: @width-base;
        margin: @margin-base auto;
        display: flex;
        .az-footer-social-icon {
            flex: 1;
            width: 20px;
            height: 20px;
            color: @az-golden-color;
            &:hover {
                cursor: pointer;
                color: @az-red-color;
            }
        }
    }
    .az-copy-right {
        margin: @margin-base auto;
        p {
            text-align: center;
            font-size: @font-size-base;
            color: @black-color;
        }
    }
}


</style>



