<template>
    <div class="az-detail">
        <div v-if="post" class="content">
            <h2>{{post.title}}</h2>
            <p>{{post.sub}}</p>
            <div class="screen-box" v-for="(img, index) in post.imgList" :key="index">
                <img class="screen-shot" :src="img">
            </div>
        </div>
        <div class="back-link">
            <router-link to="/">Back to works</router-link>
        </div>
    </div>
</template>
<script>
import { DETAIL } from '@/common/config';

export default {
    name: 'az-detail',
    data() {
        return {
            post: null,
        };
    },
    created() {
        this.fetchData();
    },
    watch: {
        '$route': 'fetchData',
    },
    methods: {
        fetchData() {
            this.post = DETAIL[this.$route.params.id];
        },
    },
};
</script>
<style lang="less">
@import '../common/css/main.less';

.az-detail {
    margin: @margin-base * 2 auto;
    max-width: @screen-lg;
    padding: @padding-base * 2;
    .content {
        width: 100%;
        h2 {
            color: @black-color;
        }
        p {
            color: @black-color;
            line-height: 2;
        }
        .screen-box {
            margin:@margin-base * 2 auto;
            width: 90%;
            .screen-shot {
                width: 100%;
                box-shadow: 0px 0px 10px @gray-color;
            }
        }
    }
    .back-link a{
        color: @az-golden-color;
        &:hover {
            color: @az-red-color;
        }
    }
}

@media only screen and (max-width:@screen-sm) {
    .az-detail {
        margin: 0 auto;
        padding: 0 @padding-base * 2;
        .screen-box {
            margin:@margin-base * 2 auto;
            width: 100% !important;
        }
        .back-link a{
            font-size: @font-size-base;
        }
    }
}

</style>



