<template>
    <div class="az-works">
        <h3>Illustration / Animation</h3>
        <div class="az-works-illustration">
            <div v-for="(item, index) in illustrations" :class="`work-${index}`" class="work">
                <router-link :to="item.route">
                    <p class="title">{{item.title}}</p>
                    <p class="sub">{{item.sub}}</p>
                </router-link>

                <div class="mask"></div>
                <img class="screen-shot" :src="item.url">
            </div>
        </div>
        <h3>UI / UX Design</h3>
        <div class="az-works-ui">
            <div v-for="(item, index) in ui" :class="`work-${index}`" class="work">
                <router-link :to="item.route">
                    <div class="container">
                        <img class="screen-shot" :src="item.url">
                    </div>
                    <p class="title">{{item.title}}</p>
                </router-link>
            </div>
        </div>
        <div class="az-work-ui-u az-work-ui-u-mask">
            <div class="work-u-box">
                <img class="screen-shot" :src="uiu.url">
            </div>
            <div class="work-u-text">
                <router-link :to="uiu.route">
                    <p class="title">{{uiu.title}}</p>
                    <p class="sub">{{uiu.sub}}</p>
                </router-link>
            </div>
        </div>
        <h3>Sketch</h3>
        <div class="az-works-sketch">
            <div v-for="(item, index) in sketch" :class="`work-${index}`" class="work">
                <div class="container">
                    <router-link :to="item.route">
                        <div class="mask"></div>
                        <img class="screen-shot" :src="item.url">
                    </router-link>
                </div>
                <p class="title">{{item.title}}</p>
            </div>
        </div>
    </div>
</template>
<script>
import { ILLUSTRATIONS, SKETCH, UI, UIU } from '@/common/config';

export default {
    name: 'az-works',
    data() {
        return {
            illustrations: ILLUSTRATIONS,
            sketch: SKETCH,
            ui: UI,
            uiu: UIU,
        };
    },
};
</script>
<style lang="less">
@import '../common/css/main.less';
.az-works {
    h3 {
        margin: @margin-base * 2;
        color: @black-color;
    }
    .az-works-illustration {
        display: flex;
        flex-wrap: wrap;
        margin: @margin-base * 2 0 @margin-base * 2 @margin-base * 2;
        .work {
            flex: 1 1 auto;
            display: inline-block;
            position: relative;
            width: @width-base * 3;
            margin: 0 @margin-base * 2 @margin-base * 2 0;
            box-shadow: 0px 0px 10px @gray-color;
            p {
                position: absolute;
                color: @white-color;
                z-index: 10;
            }
            .title {
                opacity: 0;
                font-size: 3rem;
                color: @az-light-golden-color;
                top: 30%;
                left: 10%;
                &:before {
                    position: absolute;
                    content: '';
                    display: block;
                    height: 7px;
                    width: 0;
                    top: 50%;
                    transform: translate3d(-1%, 0, 0) scaleX(1);
                    background: @white-color;
                    transition: width .3s ease, transform .3s ease-out .3s;
                    transform-origin: right center;
                }
            }
            .sub {
                opacity: 0;
                color: @az-light-golden-color;
                font-size: 1rem;
                top: calc(~"30% + 4rem");
                left: 10%;
            }
            .mask {
                position: absolute;
                background: @black-color;
                width: 100%;
                height: 100%;
                opacity: 0;
                z-index: 9;
            }
            .screen-shot {
                width: 100%;
                height: 100%;
            }
            &:hover {
                cursor: pointer;
                .mask {
                    opacity: 0.6;
                }
                .title {
                    opacity: 1;
                    &:before {
                        width: 102%;
                        transform: translate3d(-1%, 0, 0) scaleX(0);
                    }
                }
                .sub {
                    opacity: 1;
                }
            }

        }
    }
    .az-works-ui {
        display: flex;
        flex-wrap: wrap;
        margin: @margin-base * 2 0 @margin-base * 2 @margin-base * 2;
        .work {
            display: inline-block;
            margin: 0 @margin-base * 2 @margin-base * 2 0;
            p {
                color: @black-color;
            }
            .title {
                font-size: 2rem;
            }
            &:hover {
                cursor: pointer;
                .title {
                    color: @az-light-golden-color;
                }
            }
        }
        .work-0 {
            flex: 1 1 auto;
            width: @width-base;
            .container {
                width: 100%;
                .screen-shot {
                    width: 100%;
                    height: 100%;
                }
            }
        }
        .work-1,
        .work-2 {
            flex: 2 1 auto;
            width: @width-base * 2;
            .container {
                position: relative;
                // width: 100%;
                // box-shadow: 0px 0px 10px @gray-color;
                .screen-shot {
                    width: 100%;
                    height: 100%;
                }
            }
        }
    }
    .az-work-ui-u {
        width: 100%;
        // height: @height-base * 30;
        background: url('http://p26liju2m.bkt.gdipper.com/7d.png') no-repeat;
        background-size: cover;
        display: flex;
        z-index: 0;
        .work-u-box {
            flex: 1;
            width: @width-base *0.5;
            padding: @padding-base * 4 @padding-base * 2;
            .screen-shot {
                box-shadow: 0px 0px 10px @black-color;
                width: 100%;
            }
        }
        .work-u-text {
            flex: 4;
            position: relative;
            .title {
                position: absolute;
                font-size: 4rem;
                color: @white-color;
                top: 20%;
                left: 10%;
                &:before {
                    position: absolute;
                    content: '';
                    display: block;
                    height: 7px;
                    width: 0;
                    top: 50%;
                    transform: translate3d(-1%, 0, 0) scaleX(1);
                    background: @white-color;
                    transition: width .3s ease, transform .3s ease-out .3s;
                    transform-origin: right center;
                }
            }
            .sub {
                position: absolute;
                color: @white-color;
                font-size: 1rem;
                top: calc(~"20% + 6rem");
                left: 10%;
            }
            &:hover {
                cursor: pointer;
                .title {
                    color: @az-light-golden-color;
                    &:before {
                        width: 102%;
                        transform: translate3d(-1%, 0, 0) scaleX(0);
                    }
                }
                .sub {
                    color: @az-light-golden-color;
                }
            }
        }
    }
    .az-work-ui-u-mask {
        position: relative;
        &:after {
            content: "";
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            z-index: -1;
            opacity: .8;
            background-color: @black-color;
        }
    }
    .az-works-sketch {
        display: flex;
        flex-wrap: wrap;
        margin: @margin-base * 2 0 @margin-base * 2 @margin-base * 2;
        .work {
            display: inline-block;
            position: relative;
            margin: 0 @margin-base * 2 @margin-base * 2 0;

            p {
                // position: absolute;
                color: @black-color;
                z-index: 10;
            }
            .title {
                font-size: 2rem;
            }
            .sub {
                font-size: 1rem;
            }
            .mask {
                position: absolute;
                background: @gray-color;
                width: 100%;
                height: 100%;
                opacity: 0.3;
                z-index: 9;
            }
            &:hover {
                cursor: pointer;
                .mask {
                    opacity: 0;
                }
            }
        }
        .work-0,
        .work-1 {
            flex: 1 1 auto;
            width: @width-base;
            .container {
                position: relative;
                width: 100%;
                box-shadow: 0px 0px 10px @gray-color;
                .screen-shot {
                    width: 100%;
                    height: 100%;
                }
            }
        }
        .work-2 {
            flex: 2 1 auto;
            width: @width-base * 2;
            .container {
                position: relative;
                width: 100%;
                box-shadow: 0px 0px 10px @gray-color;
                .screen-shot {
                    width: 100%;
                    height: 100%;
                }
            }
        }
    }
}
@media only screen and (max-width:@screen-sm) {
    .az-works .az-work-ui-u .work-u-text {
        .title {
            font-size: 2rem;
            color: @white-color;
            top: 20%;
            left: 10%;
        }
        .sub {
            font-size: 1rem;
            top: calc(~"20% + 3rem");
            left: 10%;
        }
    }
}

</style>



