<template>
    <div class="az-header" :class="{'az-header-is-dark': isDark}">
        <router-link class="az-header-logo" :to="links.home">
            <!-- <img src="/static/img/logo.svg"> -->
        </router-link>
        <div class="az-header-menu">
            <div class="az-header-menu-item az-header-menu-work">
                <router-link :to="links.works">
                    works
                </router-link>
            </div>
            <div class="az-header-menu-item az-header-menu-contact">
                <router-link :to="links.contact">
                    contact
                </router-link>
            </div>
            <div @click="openMenu">
                <az-icon name="menu" scale="1" class="az-header-menu-icon"></az-icon>
            </div>
        </div>
        <div class="az-menu-bar" v-if="showMenu">
            <div class="link-box">
                <a class="link" @click="goWork">
                    <span>works</span>
                </a>
                <a class="link" @click="goContact">
                    <span>contact</span>
                </a>
            </div>
            <div @click="closeMenu" class="close-icon">
                <az-icon name="close" scale="1" class="az-header-menu-closs" @click="closeMenu()"></az-icon>
            </div>
        </div>
    </div>
</template>
<script>
import { LINKS } from '@/common/config';
import Icon from 'vue-awesome/components/Icon';

export default {
    name: 'az-header',
    props: ['showHeaderClose'],
    components: {
        'az-icon': Icon,
    },
    data() {
        return {
            links: LINKS,
            isDark: false,
            showMenu: false,
        };
    },
    mounted() {
        window.onscroll = () => {
            this.isDark = document.documentElement.scrollTop > window.innerHeight;
        };
    },
    methods: {
        openMenu() {
            this.showMenu = true;
        },
        closeMenu() {
            this.showMenu = false;
        },
        goWork() {
            this.showMenu = false;
            this.$router.push({ path: `${this.links.works}` });
        },
        goContact() {
            this.showMenu = false;
            this.$router.push({ path: `${this.links.contact}` });
        },
    },
};
</script>
<style lang="less">
@import '../common/css/main.less';
.az-header {
    height: @height-header;
    line-height: @line-height-medium;
    padding: @padding-header;
    margin-bottom: @margin-base * 4;
    .az-header-logo {
        display: inline-block;
        background: url(/static/img/logo.svg) no-repeat;
        background-size: contain;
        width: @width-logo;
        height: @height-logo;
        cursor: pointer;
        line-height: @line-height-medium;
    }

    .az-header-menu {
        float: right;
        height: @height-logo;
        font-size: @font-size-huge;
        .az-header-menu-item {
            display: inline-block;
            margin-left: @margin-base * 2;
            line-height: @line-height-medium;
            a {
                color: @az-golden-color;
                &.router-link-exact-active {
                    color: @az-red-color;
                }
            }

        }
        .az-header-menu-icon {
            display: none;
            color: @az-golden-color;
            line-height: @line-height-medium;
            &:hover {
                cursor: pointer;
            }
        }
    }
    .az-menu-bar {
        position: fixed;
        top: 0;
        left: 0;
        background: #fff;
        width: 100%;
        height: 100%;
        z-index: 20;
        .link-box {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            .link {
                position: relative;
                display: block;
                margin: @margin-base;
                font-size: @font-size-huge;
                color: @az-light-golden-color;
                span:before {
                    position: absolute;
                    content: '';
                    display: block;
                    height: 7px;
                    width: 0;
                    top: 50%;
                    transform: translate3d(-1%, 0, 0) scaleX(1);
                    background: @az-light-golden-color;
                    transition: width .3s ease, transform .3s ease-out .3s;
                    transform-origin: right center;
                }
                &:hover {
                    cursor: pointer;
                    color: @az-red-color;
                    span:before {
                        width: 102%;
                        transform: translate3d(-1%, 0, 0) scaleX(0);
                    }
                }

            }
        }
        .close-icon {
            position: absolute;
            top: 10%;
            left: 80%;
            color: @az-light-golden-color;
            .az-header-menu-closs {
                width: 2rem;
                height: 2rem;
            }
            &:before {
                position: absolute;
                content: '';
                display: block;
                height: 4px;
                width: 0;
                top: 70%;
                transform: translate3d(-1%, 0, 0) scaleX(1);
                background: @az-light-golden-color;
                transition: width .3s ease, transform .3s ease-out .3s;
                transform-origin: right center;
            }
            &:hover {
                cursor: pointer;
                &:before {
                    width: 102%;
                    transform: translate3d(-1%, 0, 0) scaleX(0);
                }
            }
        }
    }
}
@media only screen and (max-width:@screen-sm) {
    .az-header-logo {
        width: @width-logo * .6 !important;
        height: @height-logo * .6 !important;
    }
    .az-header-menu {
        font-size: @font-size-base !important;
    }
}

@media only screen and (max-width:@screen-md) {
    .az-header-menu-item {
        display: none !important;
    }
    .az-header-menu-icon {
        display: inline-block !important;
    }
}

</style>



